const THEME_SWITCH_STYLE_ID = "theme_switch_cli_style_id";

function switchTheme(themeName) {
    const currentTheme = window.theme_switcher_cli_themeVars.find(
        x => x.key === themeName
    );

    if (!currentTheme) {
        console.error(`can not find theme name '${themeName}'`);
    }

    if (currentTheme) {
        let oldLinkNode = document.getElementById(THEME_SWITCH_STYLE_ID);
        if (oldLinkNode) {
            // 防止样式切换中布局抖动问题
            setTimeout(() => {
                oldLinkNode.parentNode.removeChild(oldLinkNode);
            },100);
        } else {
            console.error(`link id was not found:${THEME_SWITCH_STYLE_ID}`);
        }

        createLinkNode(currentTheme);
    }
}

function createLinkNode(currentTheme) {
    let creatLink = document.createElement("link");
    creatLink.type = "text/css";
    creatLink.id = THEME_SWITCH_STYLE_ID;
    creatLink.rel = "stylesheet";
    creatLink.href = currentTheme.themePath;
    document.getElementsByTagName("head")[0].appendChild(creatLink);
}

export { switchTheme };
